.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  height: 48px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.main {
  width: 85%;
  max-width: 1200px;
  min-height: calc(100vh - 48px);
  padding: 24px 56px;
  margin: 0 auto;
}

@media screen and (max-width: 600px) {
  .main {
    width: 95%;
    padding: 24px 16px;
  }
}
